<template>
    <section>
        <ul>
           <li v-for="(item,index) in items" :key="item" class="shadow">
             <i class="checkBtn fa fa-check" aria-hidden="true"></i>
             {{item}}
             <span class="removeBtn" type="button" @click="removeTodo(item,index)">
                삭제<i class="fa fa-trash-0" aria-hidden="true"></i>
             </span>
           </li>
        </ul>
    </section>
</template>
<script>
export default {
    props:['items'],
    methods: {
       removeTodo(todoItem,index){
           this.$emit("removeTodo",todoItem,index);
       }
    },
}
</script>
<style>
  ul {
      list-style-type:none;
      padding-left:0px;
      margin-top:0;
      text-align : left;
  }

  li {
      display : flex;
      min-height:50px;
      height:50px;
      line-height:50px;
      margin: 0.5rem 0;
      padding:0 0.9rem;
      background:white;
      border-radius:5px;
  }

  .checkBtn{
      line-height: 45px;
      color:#62acde;
      margin-right:5px;
  }

  .removeBtn {
      margin-left : auto;
      color:#de4343;
  }

</style>